.popup__button {
    width: 358px;
    height: 50px;
    margin: 0 36px 35px;
    background-color: #000;
    border-radius: 2px;
    border: none;
    font-size: 18px;
    line-height: 1.2;
    font-weight: 400;
    color: #fff;
    cursor: pointer;
    
  }
  
  .popup__button:hover {
    opacity: 0.8;
  }
  
  @media screen and (max-width: 768px) {
    .popup__button {
      width: 238px;
      height: 46px;
      font-size: 14px;
      margin: 0 22px 25px;
    }
  }